import React, { useEffect, useState } from 'react';
import { Box, Grid, Container } from '@mui/material';
import Sidebar from '../components/Sidebar';
import Header from '../components/Header';
import FiltersPanel from '../components/FiltersPanel';
import ChartsPanel from '../components/ChartsPanel';
import ItemsTable from '../components/ItemsTable';
import { fetchItems } from '../utils/api';
import { useDashboard } from '../context/DashboardContext';

export default function DashboardPage() {
  const { filters, setFilters } = useDashboard();
  const [data, setData] = useState({ items: [], total: 0, page: 1, pageSize: 10 });
  const [loading, setLoading] = useState(false);

  const load = async () => {
    setLoading(true);
    try {
      const res = await fetchItems(filters);
      setData({
        items: res.items,
        total: res.total,
        page: res.page,
        pageSize: res.pageSize,
      });
    } catch (err) {
      console.error(err);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    load();
  }, [
    filters.page, filters.pageSize, filters.search, filters.category,
    filters.status, filters.startDate, filters.endDate, filters.sortBy, filters.sortDir
  ]);

  return (
    <Box sx={{ display: 'flex', minHeight: '100vh', bgcolor: '#f7f8fa' }}>
      
      {/* ---------------- SIDEBAR ---------------- */}
      <Box
        sx={{
          width: 200,
          minHeight: '100vh',
          bgcolor: '#1e1e2f',
          color: 'white',
          p: 2,
          position: 'fixed',      // Sidebar stays fixed
          left: 0,
          top: 0,
          bottom: 0,
        }}
      >
        <Sidebar />
      </Box>

      {/* ---------------- MAIN CONTENT ---------------- */}
      <Box
        component="main"
        sx={{
          flexGrow: 1,
          ml: '240px',   // MAIN CONTENT shifted to right of fixed sidebar
          display: 'flex',
          flexDirection: 'column',
          minHeight: '100vh',
        }}
      >
        <Header />

        <Container maxWidth="xl" sx={{ mt: 2, mb: 4 }}>
          <Grid container spacing={2}>
            
            {/* Filters */}
            <Grid item xs={12} md={3}>
              <FiltersPanel onFilterChange={() => {
                setFilters(prev => ({ ...prev, page: 1 }));
              }} />
            </Grid>

            {/* Charts */}
            <Grid item xs={12} md={9}>
              <ChartsPanel items={data.items} loading={loading} />
            </Grid>

            {/* Table */}
            <Grid item xs={12}>
              <ItemsTable
                items={data.items}
                total={data.total}
                page={data.page}
                pageSize={data.pageSize}
                loading={loading}
                onPageChange={(page) =>
                  setFilters(prev => ({ ...prev, page }))
                }
                onPageSizeChange={(pageSize) =>
                  setFilters(prev => ({ ...prev, pageSize, page: 1 }))
                }
                onSortChange={(sortBy, sortDir) =>
                  setFilters(prev => ({ ...prev, sortBy, sortDir }))
                }
              />
            </Grid>

          </Grid>
        </Container>
      </Box>
    </Box>
  );
}
